import React, { Component } from 'react';
//import logo from './logo.svg';
import './App.css';
import { push as Menu } from 'react-burger-menu'
import FullCanvas from './FullCanvas'
import AngleInput from './AngleInput'
import InputField from './InputField'
import RadioConvertUnits from './RadioConvertUnits'
//import RadioVelocityUp from './RadioVelocityUp'
import { crunchnumbers } from './crunchnumbers.js'
import arrow from './arrow.png'


function to_meters(feet) //defined exactly by the National Bureau of Standards.
{ return feet * 0.3048 } //Is the definition of a foot.
						 //I can't get more accurate than that.

function to_feet(meters)			//The inverse calculates to more decimals...
{ return meters * 3.2808333333 }

//round to the nearest thousandth.
function round(num, places) {
    if(places==null)
	{
		places=3;
	}
	
	var multiplier = Math.pow(10, places);
    var output = Math.round(num * multiplier) / multiplier;
	
	//fix some weird output. Probably due to feet/meters conversion not being more precise.
	if(Math.abs(output - Math.round(output)) <= .0011)
	{ output = Math.round(output); }

	return output;	
}


class App extends Component {

   constructor(props) {
      super(props);
		
      this.state = {
		  diameter:50,
		  startheight:4,
		  units:"ft",
		  gravity:100,
		  mode:"anglevel",
		  thrownUp:3,
		  accel_earth:32.174,
		  angle:0,
		  relative_v_y: Math.sqrt( 2 * 32.174 * 3 ),
		  relative_v_x: 0,
		  expectedtime:0,
		  expectedheight:0,
		  standingvelocity: Math.sqrt( 32.174 * 25),
		  omega: Math.sqrt( 32.174 / 25),
		  g_accel: 32.174,
		  speed: 13.894,
		  percenttime:100,
		  menuLeftOpen: false,
		  droppedflag: false,
      }

      this.updateState = this.updateState.bind(this);
      this.setInitialState = this.setInitialState.bind(this);
      this.updateOtherVars = this.updateOtherVars.bind(this);
	  this.convertunits = this.convertunits.bind(this);
	  this.convertmode = this.convertmode.bind(this);
	  this.updateDiameter = this.updateDiameter.bind(this);
	  this.updateThrowHeight = this.updateThrowHeight.bind(this);
	  this.updateVelocity = this.updateVelocity.bind(this);
	  this.updateAngle = this.updateAngle.bind(this);
	  this.isMenuLeftOpen = this.isMenuLeftOpen.bind(this);
	  this.isMenuRightOpen = this.isMenuRightOpen.bind(this);
	  
	  //this.updateOtherVars();
	}
	
	setInitialState()
	{
      this.setState({
		  diameter:50,
		  startheight:4,
		  units:"ft",
		  gravity:100,
		  mode:"anglevel",
		  thrownUp:3,
		  accel_earth:32.174,
		  angle:0,
		  relative_v_y: Math.sqrt( 2 * 32.174 * 3 ),
		  relative_v_x: 0,
		  expectedtime:0,
		  expectedheight:0,
		  standingvelocity: Math.sqrt( 32.174 * 25),
		  omega: Math.sqrt( 32.174 / 25),
		  g_accel: 32.174,
		  speed: 13.894,
		  percenttime:100,
		  droppedflag: false,		  
		}, ()=>{this.updateOtherVars();} )
		
	}

	componentDidMount() {
		this.updateOtherVars();
	}


   updateState(value, variable)
   {   this.setState({[variable]: value,
		}, ()=>{this.updateOtherVars();} )
   }  

   convertunits(e)
   {	
		var accel_earth
		if(e.currentTarget.value==="ft") {
			accel_earth = 32.174; 
			this.setState( {
				startheight : round(to_feet(this.state.startheight)),
				diameter : round(to_feet(this.state.diameter)),
				thrownUp : round(to_feet(this.state.thrownUp)),
				speed : round(to_feet(this.state.speed)),
				relative_v_x : round(to_feet(this.state.relative_v_x)),
				relative_v_y : round(to_feet(this.state.relative_v_y)),				
				
			})

		}
		else { 
			accel_earth = 9.80665; 
			this.setState( {
				startheight : round(to_meters(this.state.startheight)),
				diameter : round(to_meters(this.state.diameter)),
				thrownUp : round(to_meters(this.state.thrownUp)),
				speed : round(to_meters(this.state.speed)),
				relative_v_x : round(to_meters(this.state.relative_v_x)),
				relative_v_y : round(to_meters(this.state.relative_v_y)),
			})
		}
								   
	   this.setState( {units: e.currentTarget.value,
					   accel_earth: accel_earth,
					   g_accel: accel_earth * this.state.gravity/100 
		}, ()=>{this.updateOtherVars();} )
	}

   convertmode(e)  //no longer used, but kept here in case I want to revert.
   {   this.setState( {mode: e.currentTarget.value,} );   
	   
	   if(e.currentTarget.value==="thrownUp")
	   { this.setState( {relative_v_x:0,} ) }
   }   
   
   //the starting height must NEVER be greater than the diameter!
   //Thus this field gets its own handling function.
   updateDiameter(value, variable)
   {
	   
	   if(value < this.state.thrownUp) {
		   this.setState({diameter: value,
						  startheight: value,
			}, ()=>{this.updateOtherVars();} )
	   } 
	   else if( this.state.diameter < 10 && value > 10 && this.state.startheight < 4 && this.state.units==="ft")
	   {
		   this.setState({diameter: value,
						  startheight: 4,
			}, ()=>{this.updateOtherVars();} )		   
	   }
	   else if( this.state.diameter < 2 && value > 2 && this.state.startheight < 1 && this.state.units==="m")
	   {
		   this.setState({diameter: value,
						  startheight: 1.219
,
			}, ()=>{this.updateOtherVars();} )		   
	   }
	   else {
		   this.setState({diameter: value,
			}, ()=>{this.updateOtherVars();} )		   
	   }
   }
   
   updateThrowHeight(value, variable)
   {
	   var speed = Math.sqrt( 2 * this.state.accel_earth * value );
		this.setState( {
			thrownUp: value,
			relative_v_y: speed,
			relative_v_x: 0,
			angle:0,
			speed: speed,
		}, ()=>{this.updateOtherVars();} )
   }
   
	updateVelocity(value, variable)
	{
	    var computedangle = (-1* this.state.angle * Math.PI / 180) + (Math.PI / 2);
		var droppedflag = (value===0)
		
		this.setState( {
			relative_v_x: value * Math.cos(computedangle),
			relative_v_y: value * Math.sin(computedangle),
			speed: value,
			droppedflag: droppedflag,
		}, ()=>{this.updateOtherVars();} )
	}
	
	updateAngle(e)
	{
	    var computedangle = (-1* e.target.value * Math.PI / 180) + (Math.PI / 2);

		this.setState( {
			angle: Number(e.target.value),
			relative_v_x: this.state.speed * Math.cos(computedangle),
			relative_v_y: this.state.speed * Math.sin(computedangle),
		}, ()=>{this.updateOtherVars();} )
	}

	
	updateOtherVars()
	{
		var expectedheight
		var expectedtime
		
		if(this.state.relative_v_y > 0)
		{			
			//var expectedheight = height_start + height_thrown; //works out exactly the same for case throw "up"

			expectedheight = (Math.pow(this.state.relative_v_y,2) / (2 * this.state.accel_earth)) + this.state.startheight;
			expectedtime = Math.sqrt( 2 * expectedheight / this.state.accel_earth ) + ( this.state.relative_v_y / this.state.accel_earth)  //same formula as throw "up"
		}
		else if(this.state.relative_v_y <= 0)
		{
			expectedheight = this.state.startheight;
			
			var end_v_y = -1 * Math.sqrt( Math.pow(this.state.relative_v_y,2) + 2*this.state.accel_earth*this.state.startheight )  //temporary variable
			expectedtime = Math.abs(2 * expectedheight / (this.state.relative_v_y + end_v_y))
		}

		var g_accel = this.state.accel_earth * this.state.gravity / 100
		
		var omega = Math.sqrt( g_accel / (this.state.diameter/2) )
		var standingvelocity = Math.sqrt( this.state.g_accel * (this.state.diameter/2) )
		
		var r_oncoin = (this.state.diameter/2) - this.state.startheight
		var g_oncoin = omega*omega*r_oncoin;
		
		var standingcoin = -1 * Math.sqrt( g_oncoin * r_oncoin ) //-1 b/c station is rotating clockwise
		var start_v_x = this.state.relative_v_x + standingcoin
		
		var answers = crunchnumbers(this.state.diameter/2, omega, start_v_x, this.state.relative_v_y, r_oncoin)
		
		this.setState({
			expectedtime : expectedtime,
			expectedheight: expectedheight,
			standingvelocity: standingvelocity,
			omega: omega,
			g_accel: g_accel,
			maxheight:answers.maxheight,
			x_f : answers.x_f,
			y_f : answers.y_f,
			time: answers.time,
			slope: answers.slope,
			total_difference: answers.total_difference,
			start_v_x: start_v_x,
		})
	}
	
	isMenuLeftOpen(state) {
		this.setState( {
			menuLeftOpen: state.isOpen,
		} )
	}
	
	isMenuRightOpen(state) {
		this.setState( {
			menuRightOpen: state.isOpen,
		} )
	}
   
  render() {

	var showUp = true;
	var showVelocity = false;
	var showAngle = false;
	
	if(this.state.mode==="up") {
		showUp = true;
		showVelocity = false;
		showAngle = false;
	}
	else
	{
		showUp = false;
		showVelocity = true;
		showAngle = true;
	}
	
	var fullunits
	if(this.state.units==="ft")
	{ fullunits = "feet" }
	else
	{ fullunits = "meters" }
	
	const initialwidth = { width:'initial', }
	
	var displaylabel
	if(this.state.menuLeftOpen || this.state.menuRightOpen)
	{ displaylabel="hide" }
	else
	{ displaylabel="" }
	
    return (
    
<div className="App" id="outer-container">

	<Menu pageWrapId={ "page-wrap" } burgerButtonClassName={ "left-button" } outerContainerId={ "outer-container" } onStateChange={ this.isMenuLeftOpen }  >
		<div><h3>Input Values</h3></div>
		<RadioConvertUnits onChange={this.convertunits} units={this.state.units} />

		<InputField title="Height thrown up on Earth" value={this.state.thrownUp} min={0} updateState={this.updateThrowHeight} variable="thrownUp" units={this.state.units} display={showUp} />

		<InputField title="Starting height" min={0} max={this.state.diameter} updateState={this.updateState} value={this.state.startheight} variable="startheight" units={this.state.units}/>

		<AngleInput title="Angle from vertical" value={this.state.angle} min="-180" max="180" updateState={this.updateAngle} variable="angle" units="" droppedflag={this.state.droppedflag} display={showAngle} />		
		
		<InputField title="Throw speed" value={this.state.speed} min={0} max={(this.state.standingvelocity+10) * 100}    updateState={this.updateVelocity} variable="speed" units={this.state.units+"/s"} display={showVelocity} />
		
		<table><tbody><tr><td><div className="randomFact">Height coin reaches<br/>if thrown on Earth</div></td><td><div className="UnitsStyle" style={initialwidth}>{round(this.state.expectedheight,1)}&nbsp;{this.state.units}</div></td></tr></tbody></table>

		<InputField title="Station diameter" value={this.state.diameter} min={1} max={1000000000000} updateState={this.updateDiameter} variable="diameter" units={this.state.units}/>
		
		<InputField title="Gravity at floor" min={0} max={2000} updateState={this.updateState} variable="gravity" value={this.state.gravity} units="%"/>

		<InputField title="Playback speed" min={0} max={Math.ceil(this.state.time)*100} updateState={this.updateState} variable="percenttime" value={this.state.percenttime} units="%"/>

		
		<ResetButton updateState={this.setInitialState}/>
		
	</Menu>

	<Menu right pageWrapId={ "page-wrap" } outerContainerId={ "outer-container" } burgerButtonClassName={ "right-button" } onStateChange={ this.isMenuRightOpen } ></Menu>
	<div id="page-wrap">
		<div className="navbar">A Coin Toss on a Virtual Space Station</div>
		<div id="canvasWrapper" >
			<FullCanvas slope={this.state.slope} time={this.state.time} radius={this.state.diameter/2} omega={this.state.omega} height_start={this.state.startheight} start_v_x={this.state.start_v_x} start_v_y={this.state.relative_v_y} units={this.state.units} menuLeftOpen={this.state.menuLeftOpen} menuRightOpen={this.state.menuRightOpen} percenttime={this.state.percenttime} />
			
			<a href="https://priceisright204.github.io/coriolis.html">
				<div className="referenceLink">What is this page?</div></a>
			<div className="fixed">
				<div id="scaleunits"><i>Scale in {fullunits}</i></div>
				<div className="smallBreak"><br/></div>
				<div className="bubble"><div>
					This is the <b><i>thrower's</i></b> perspective.<br/>The floor looks stationary to him.<br/>
					Through a window, he sees <b>outer space</b> rotating counterclockwise.</div>
						<img src={arrow} alt="curved-arrow" height="25" width="100"/></div>
				</div>
			</div>
			<div id="inputlabel" className={displaylabel}>Input</div>
			<div id="outputlabel" className={displaylabel}>Output</div>
	</div>
	
</div>

    );
  }
}

//	<RadioVelocityUp onChange={this.convertmode} units={this.state.mode} /> //no longer used


class ResetButton extends Component {
	render() {
		
	return (
		<button type="submit" className="ResetButton" onClick={ this.props.updateState }>Reset</button>
		)
  }
}

export default App;



