import React, { Component } from 'react';
//import ContainerDimensions from 'react-container-dimensions';
	//cannot obtain a ref from a functional component,
	//thus cannot get the context.

import './App.css';
import ReactDOM from 'react-dom'
import imagefile from './silhouette.png'

var timeouts = {
	moveinterval : null,
	repeattimeout : null,
	sourceready: false,
	delayrefresh: null,
}



class FullCanvas extends Component {
	
	constructor() {
		super();
		this.state =  { width:0,
						height:0, 
						}
		
		this.updateCanvasSize = this.updateCanvasSize.bind(this);
		
		window.addEventListener('resize', () => { this.updateCanvasSize(); });
		this.loadImage = this.loadImage.bind(this)
		//this.drawCanvas = this.drawCanvas.bind(this)		
	}
	
	componentDidMount()
	{
		this.loadImage()
	}
	
	loadImage()
	{
		this.silhouette = new window.Image();
		this.silhouette.onload = this.onImageLoaded.bind(this)
		this.silhouette.src = imagefile //require('silhouette.png')
	}
	
	onImageLoaded()
	{
		//console.log("image loaded!")
		this.setState({sourceReady:true});
		this.updateCanvasSize()		
	}
	
	updateCanvasSize() //called by window resize event
	{
		const parentNode = ReactDOM.findDOMNode(this).parentNode

		this.setState({
			width: parentNode.clientWidth,
			height: parentNode.clientHeight,		
		}, ()=>{this.componentDidUpdate();})		
	}
	
	//was a bad idea.
	/*componentDidUpdate()
	{
		if(timeouts.delayrefresh!==null)
		{
			clearTimeout(timeouts.delayrefresh);
			timeouts.delayrefresh=null;
		}
		
		timeouts.delayrefresh = setTimeout(this.drawCanvas, 50) //1/20 second delay
		
	}*/
	
	componentDidUpdate()
	{
		console.log("canvas update")
		
		//let blah = ReactDOM.findDOMNode(this.refs.blah);
		clearInterval(timeouts.moveinterval); timeouts.moveinterval=null;
		clearTimeout(timeouts.repeattimeout); timeouts.repeattimeout=null;		
		
		let canvas = ReactDOM.findDOMNode(this.refs.myCanvas);
		let ctx = canvas.getContext('2d');

		//const ctx = this.refs.canvas.getContext('2d');
		//const ctx = this.context
		var dimensions = {}
		dimensions.units = this.props.units
		dimensions.width = ctx.canvas.clientWidth;
		dimensions.height = ctx.canvas.clientHeight;
		dimensions.X_BUFFER = dimensions.width/10
		dimensions.Y_BUFFER = dimensions.height/15
		dimensions.DRAWING_WIDTH = dimensions.width - 2 * dimensions.X_BUFFER
		dimensions.DRAWING_HEIGHT = dimensions.height - 2 * dimensions.Y_BUFFER
		dimensions.DELAY_BETWEEN_DRAWS = 2.5
		dimensions.menuLeftOpen = this.props.menuLeftOpen
		dimensions.menuRightOpen = this.props.menuRightOpen
		
		var relativepoints = getPoints(this.props.slope, this.props.time, this.props.radius, this.props.omega, this.props.height_start, this.props.start_v_x, this.props.start_v_y)
				
		var resources = prep_canvas(relativepoints, dimensions, this.props.time, this.props.percenttime);
		
		reset_canvas(ctx, this.props.radius, resources.minmaxes, dimensions, relativepoints[relativepoints.length-1], this.silhouette, this.props.height_start);
		if(this.props.menuLeftOpen==true || this.props.menuRightOpen==true || this.props.percenttime == 0)
		{
			draw_curve_static(ctx, resources.canvaspoints, dimensions)
		}
		else
		{
			draw_curve_active(ctx, resources.canvaspoints, dimensions, this.props.time, this.props.percenttime, this.props.radius, resources.minmaxes, relativepoints[relativepoints.length-1], this.silhouette, this.props.height_start)
		}
		
	}
	
	render() {
		
	return(
		<canvas ref="myCanvas" width={this.state.width} height={this.state.height} />
	  );
	} 
	
}


//round to the nearest thousandth.
function round(num, places) {
    if(places==null)
	{
		places=3;
	}
	
	var multiplier = Math.pow(10, places);
    var output = Math.round(num * multiplier) / multiplier;
	
	//fix some weird output. Probably due to feet/meters conversion not being more precise.
	if(Math.abs(output - Math.round(output)) <= .0011)
	{ output = Math.round(output); }

	return output;	
}



function getPoints(slope, time, radius, omega, height_start, start_v_x, start_v_y)
{
	
	var absolutepointslist = []
	
	//get ~240 increments per second, but min 240 increments, max 3000 increments.
	//It won't SHOW that many, but if the user shows at 50% speed, then I'll want them.
	var time_increment
	if( time<1.2 )
	{ time_increment = time/240 }
	else if( time>30 )
	{ time_increment = time/3000 }
	else
	{ time_increment = .0044; }
	
	//var time_increment = Number(time.div(150))

	var absolutePoints
	if( isFinite(slope) && time > 0)
	{
		var t
		for(t=0; t<=Number(time); t+=time_increment)
		{
			absolutePoints = new AbsolutePoints( t, Number(radius), Number(omega), Number(slope), Number(height_start), Number(start_v_x) )
			absolutepointslist.push(absolutePoints)
		}
		
		//do it once more at t = time.
		absolutePoints = new AbsolutePoints( Number(time), Number(radius), Number(omega), Number(slope), Number(height_start), Number(start_v_x) );
		absolutepointslist.push( absolutePoints );			
	}
	else if(!isFinite(slope) && time > 0)
	{
		for(t=0; t<=Number(time); t+=time_increment)
		{
			absolutePoints = new AbsolutePointsVertical( t, Number(radius), Number(omega), Number(slope), Number(height_start), Number(start_v_y) )
			absolutepointslist.push(absolutePoints)
		}
		
		//do it once more at t = time.
		absolutePoints = new AbsolutePointsVertical( Number(time), Number(radius), Number(omega), Number(slope), Number(height_start), Number(start_v_y) );
		absolutepointslist.push( absolutePoints );
	}
	else if(time==0)
	{
		absolutePoints = new AbsolutePoints( 0, Number(radius), Number(omega), Number(slope), Number(height_start), Number(start_v_y) )
		absolutepointslist.push(absolutePoints) //push exactly one point
	}
	else if(!isFinite(time) )
	{
		absolutePoints = new AbsolutePointsVertical( 0, Number(radius), Number(omega), Number(slope), Number(height_start), Number(start_v_y) )
		absolutepointslist.push(absolutePoints)
	}	
	//point = new RelativePoint( Number(time), Number(radius), Number(omega), Number(slope), Number(height_start), Number(start_v_x) )
	
	//point = new RelativePoint( 0.25, Number(radius), Number(omega), Number(slope), Number(height_start), Number(start_v_x) )
	
	var relativepoints = []
	for(var i=0; i<absolutepointslist.length; i++)
	{
		relativepoints.push( new RelativePoint( absolutepointslist[i] ) )
	}
	
	return relativepoints
}


function draw_curve_active(ctx, canvaspoints, dimensions, time, percenttime, radius, minmaxes, lastpoint, silhouette, height_start)
{
	if(timeouts.moveinterval!=null)
	{	
		clearInterval(timeouts.moveinterval); timeouts.moveinterval=null;
		clearTimeout(timeouts.repeattimeout); timeouts.repeattimeout=null; }

	reset_canvas(ctx, radius, minmaxes, dimensions, lastpoint, silhouette, height_start);
	
	var i=1;
	
	//draw a little circle where the coin *started*.
	ctx.beginPath();
	ctx.strokeStyle="#5c6870";
	ctx.arc(canvaspoints[0].x+dimensions.X_BUFFER, canvaspoints[0].y+dimensions.Y_BUFFER,5,0,2*Math.PI);
	ctx.stroke();

	ctx.strokeStyle="#000000"; //reset the stroke style to black.
	
	//output ~38fps
	const framespersecond = 38
	var pointspersecond = canvaspoints.length / ( time * (100/percenttime)  )
	var pointsperframegoal = pointspersecond / framespersecond
	var pointsperframeactual = Math.ceil(pointsperframegoal)
	var offset = pointsperframeactual / pointsperframegoal
	var time_delay_in_ms = (1/framespersecond) * offset * 1000
	
	
	
	//Timeouts are better than intervals because they can be adjusted mid-draw,
	//and they will not interfere with each other.
	function moveline() {
		if(i>=canvaspoints.length)
		{  clearInterval(timeouts.moveinterval); timeouts.moveinterval=null; 
			//necessary so you don't have to wait forever
			//if you adjusted the percent time mid-draw.

			timeouts.repeattimeout = setTimeout(function() {
				draw_curve_active(ctx, canvaspoints, dimensions, time, percenttime, radius, minmaxes, lastpoint, silhouette, height_start); }, dimensions.DELAY_BETWEEN_DRAWS * 1000);
			return; 
		}

		ctx.beginPath();
		ctx.moveTo(canvaspoints[i-1].x+dimensions.X_BUFFER, canvaspoints[i-1].y+dimensions.Y_BUFFER);
		
		for(var x=0; x<pointsperframeactual; x++)
		{
			if(i>=canvaspoints.length)
			{  clearInterval(timeouts.moveinterval); timeouts.moveinterval=null; 
				//necessary so you don't have to wait forever
				//if you adjusted the percent time mid-draw.
				ctx.stroke();

				timeouts.repeattimeout = setTimeout(function() {
					draw_curve_active(ctx, canvaspoints, dimensions, time, percenttime, radius, minmaxes, lastpoint, silhouette, height_start); }, dimensions.DELAY_BETWEEN_DRAWS * 1000);
				return; 
			}

			ctx.lineTo(canvaspoints[i].x+dimensions.X_BUFFER, canvaspoints[i].y+dimensions.Y_BUFFER)
			

			i+=1;
		}
		ctx.stroke();
	};
	
	timeouts.moveinterval = setInterval(moveline, time_delay_in_ms);

}


function draw_curve_static(ctx, canvaspoints, dimensions)
{

	//draw a little circle where the coin *started*.
	ctx.beginPath();
	ctx.strokeStyle="#5c6870";
	ctx.arc(canvaspoints[0].x+dimensions.X_BUFFER, canvaspoints[0].y+dimensions.Y_BUFFER,5,0,2*Math.PI);
	ctx.stroke();
	
	//now render all of the canvaspoints for the coin's calculated path.
	ctx.beginPath();
	ctx.strokeStyle="#000000";
	ctx.moveTo(canvaspoints[0].x+dimensions.X_BUFFER, canvaspoints[0].y+dimensions.Y_BUFFER);
	
	for(var i=1; i<canvaspoints.length; i++)
	{		
		ctx.lineTo(canvaspoints[i].x+dimensions.X_BUFFER, canvaspoints[i].y+dimensions.Y_BUFFER)
	}
	
    ctx.stroke();
}




//increment the scale nicely to units of 1,2,4,5,10,20,40,50,100,...
//more intuitive for the user.
function scale_increment() {
	this.series = -1;
		
	this.run = function(value) {
		this.series+=1;
		if(this.series==4)
		{
			this.series=0;
		}
		
		if(this.series!=2)
		{
			return value*=2
		}
		else
		{
			return value+=value/4
		}
	}
}


//generate the positions for a scale mark,
//given the angle on a circle where you want to display it and a labeled "value"
function scalemark(radius, angle, minmaxes, value, dimensions)
{
	//mark size 10px, means 5px above and below station curve
	//subtract the proper number of feet-per-pixel from the radius to achieve.
	var x1 = (radius - (5 / minmaxes.pxperfoot)) * Math.cos(angle)
	var x2 = (radius + (5 / minmaxes.pxperfoot)) * Math.cos(angle)
	//var x3 = (radius + (30 / minmaxes.pxperfoot)) * Math.cos(angle)
	
	var y1 = (radius - (5 / minmaxes.pxperfoot)) * Math.sin(angle) + radius
	var y2 = (radius + (5 / minmaxes.pxperfoot)) * Math.sin(angle) + radius
	//var y3 = (radius + (20 / minmaxes.pxperfoot)) * Math.sin(angle) + radius
	
	this.start = new CanvasPoint(minmaxes, x1, y1, dimensions)
	this.end = new CanvasPoint(minmaxes, x2, y2, dimensions)
	
	this.angle=angle
	
	var length = value.toString().length;
	var canvasX3 = this.end.x - length*3 + length*3*Math.cos(angle) + 10*Math.cos(angle)
	var canvasY3 = this.end.y - 15*Math.sin(angle) + 3*(Math.sin(angle)+1)
				//the draw coord starts from the bottom. adjust for that.
				//the more "up" on the circle, the more it matters.
				
	this.numberspot = {x : canvasX3, y : canvasY3}
	//this.numberoffsetX = value.toString().length*5
	//this.numberoffsetY = -5 * Math.cos(angle)
	this.value = value
}



function draw_scale(ctx, radius, minmaxes, dimensions, lastpoint)
{
	
	const scale_color = "#000000"
	
	//Label the scale so people know what they're looking at.
	/*if(MyLib.units=="ft")
	{  document.getElementById("scalemessage").innerHTML = "<sup><i>Scale in feet</i></sup>"  }
	else
	{  document.getElementById("scalemessage").innerHTML = "<sup><i>Scale in meters</i></sup>"  }*/
	
	//var radius = MyLib.radius;
	//var minmaxes = MyLib.minmaxes;
	
	//the last point is the relative landing position of the coin,
	//which is where the scale should go to.
	//var lastpoint = MyLib.relativepoints[MyLib.relativepoints.length-1];

	//angle swept from person to coin's landing position.
	var half_chord_value = lastpoint.dist/(2*radius)
	if(half_chord_value > 1) { half_chord_value = 1 }  //a bugfix
	var chord_angle = 2 * Math.asin(half_chord_value)
	
	//var chord_angle = lastpoint.theta_pc //is apparently not equivalent?

	//var curve_dist = radius * chord_angle //total scale distance
											//variable never used.
	
	var start = 3*Math.PI/2  //starting scale angle, always at 3PI/2 (270D)
	
	//show the scale in both directions! 
	//If the coin goes the other way, then display the scale the other way.
	var end
	if(lastpoint.x < 0)
	{	end = start - chord_angle  } //end scale angle
	else
	{	end = start + chord_angle  }


	//Match the scale to the canvas. Calculate arc-per-foot, px-per-foot.
	//Or METERS...the math works out the same. I'm not changing variable names.
	//arcperfoot is the variable actually used to calculate tic locations.
	//Pxperfoot is used to generate a scale range that would actually be
	//human-interpretable.
	var arc_per_foot = 1 / radius
	
	//Pick a scale distance that fits with > 20 pixels between each tick.
	//Intelligently increment the scale interval to something human-interpretable.
	//Goes 1, 2, 4, 5, 10, 20, 40, 50, 100, 200, 400, 500, 1000...
	var increment=1;
	var increaseval = new scale_increment()
	
	while(minmaxes.pxperfoot*increment<20)
	{
		increment = increaseval.run(increment);
	}

	//if the numbers are long, give it extra space
	//by incrementing the interval a bit more.
	if(increment.toString().length >= 3 || (chord_angle * radius) > 1000)
	{	while(minmaxes.pxperfoot*increment<40)
		{
			increment = increaseval.run(increment);
		}
	}
	
	//generate the locations for the tic marcks, 
	//up to the end spot plus one tic.
	//coded for both a positive arc and a negative arc, depending
	//which way the coin went.
	var i=0;
	var scalemarks = [];
	var a
	if(lastpoint.x < 0)
	{
		//incrementing over an angle, so I called it a for angle.
		for(a=start; a>end-(arc_per_foot*increment); a-=(arc_per_foot*increment))
		{
			scalemarks.push(new scalemark(radius, a, minmaxes, i, dimensions) )
			i+=increment;
		}
	}
	else
	{
		for(a=start; a<end+(arc_per_foot*increment); a+=(arc_per_foot*increment))
		{
			scalemarks.push(new scalemark(radius, a, minmaxes, i, dimensions) )
			i+=increment;
		}
	}
	
	if(scalemarks.length===2)
	{
		var angle=((scalemarks[1].angle - start) * -1) + start
		scalemarks.push(new scalemark(radius, angle, minmaxes, scalemarks[1].value, dimensions) )
	}

	//draw the marks	
	ctx.font = "14px Arial";

	ctx.beginPath();
	ctx.fillStyle = scale_color;
	ctx.strokeStyle = scale_color;
	for(i=0; i<scalemarks.length; i++)
	{
		var mark = scalemarks[i]
		ctx.moveTo(mark.start.x+dimensions.X_BUFFER, mark.start.y+dimensions.Y_BUFFER)
		ctx.lineTo(mark.end.x+dimensions.X_BUFFER, mark.end.y+dimensions.Y_BUFFER)
		ctx.fillText(mark.value.toString(), mark.numberspot.x+dimensions.X_BUFFER, mark.numberspot.y+dimensions.Y_BUFFER);
	}
	ctx.stroke()
	
	ctx.fillStyle="#000000"
	ctx.strokeStyle="#000000"
}



//erase canvas, draw the floor, and draw the scale if applicable.
function reset_canvas(ctx, radius, minmaxes, dimensions, lastpoint, silhouette, height_start) {
	
	ctx.clearRect(0, 0, dimensions.width, dimensions.height);
	
	draw_floor(ctx, radius, minmaxes, dimensions)
	
	if(silhouette!=null && (height_start==4 || height_start==1.219) )
	{	draw_silhouette(ctx, silhouette, minmaxes, dimensions, height_start);   }
	else
	{   canvas_arrow(ctx, minmaxes, dimensions, height_start) }
	
	draw_scale(ctx, radius, minmaxes, dimensions, lastpoint)
	
}

function canvas_arrow(ctx, minmaxes, dimensions, height_start) {	 //, fromx, fromy, tox, toy){
    
	var origin = new CanvasPoint(minmaxes, 0, 0, dimensions)
	var start = new CanvasPoint(minmaxes, 0, height_start, dimensions)

	var fromx = origin.x + dimensions.X_BUFFER
	var fromy = origin.y + dimensions.Y_BUFFER
	var tox = start.x + dimensions.X_BUFFER
	var toy = start.y + dimensions.Y_BUFFER + 7 //+7 so it's not dwarfing the coin.
	
	var headlen = 10;   // length of head in pixels
    var angle = Math.atan2(toy-fromy,tox-fromx);
	
	var pxheight = fromy - toy
	if(pxheight > 15)
	{
		ctx.beginPath()
		ctx.strokeStyle="red"
		ctx.fillStyle="red"
		ctx.lineWidth=2
		ctx.moveTo(fromx, fromy);
		ctx.lineTo(tox, toy);
		ctx.lineTo(tox-headlen*Math.cos(angle-Math.PI/6),toy-headlen*Math.sin(angle-Math.PI/6));
		ctx.moveTo(tox, toy);
		ctx.lineTo(tox-headlen*Math.cos(angle+Math.PI/6),toy-headlen*Math.sin(angle+Math.PI/6));

		ctx.fillText(round(height_start, 1).toString() + " " + dimensions.units, fromx + 5, fromy - pxheight/2);
		ctx.stroke()
		
		ctx.strokeStyle="#000000";
		ctx.fillStyle="#000000";
		ctx.lineWidth=1;
	}
}

function draw_silhouette(ctx, silhouette, minmaxes, dimensions, height_start)
{
	//drawing is dimensions [323,831] (width, height).
	//Need the spot (24, 539) (from bottom) to cooincide
		//with *relative* coordinate (0,4), aligned to the canvas.

	//var footbuffer = 2
	
	var origin = new CanvasPoint(minmaxes, 0, 0, dimensions)
	var px_height_start = minmaxes.pxperfoot * height_start
	var numpixtotop = (px_height_start * 830/539)
	var drawing_top = origin.y - numpixtotop
	var px_width = numpixtotop * 323/831
	var px_offset_left = px_width * 24/323
	
	var drawing_left
	if(minmaxes.directionLeft)
	{
		drawing_left = origin.x - px_offset_left	
		drawImage(ctx, silhouette, drawing_left + dimensions.X_BUFFER, drawing_top + dimensions.Y_BUFFER, px_width, numpixtotop);
	}
	else
	{
		drawing_left = origin.x + px_offset_left
		drawImage(ctx, silhouette, drawing_left + dimensions.X_BUFFER-px_width, drawing_top + dimensions.Y_BUFFER, px_width, numpixtotop, 0, true);
	}
}


function drawImage(ctx, img, x, y, width, height, deg, flip, flop, center)
{
	ctx.save();

	if(typeof width === "undefined") width = img.width;
	if(typeof height === "undefined") height = img.height;
	if(typeof center === "undefined") center = false;

	// Set rotation point to center of image, instead of top/left
	if(center) {
		x -= width/2;
		y -= height/2;
	}

	// Set the origin to the center of the image
	ctx.translate(x + width/2, y + height/2);

	// Rotate the canvas around the origin
	var rad = 2 * Math.PI - deg * Math.PI / 180;    
	ctx.rotate(rad);

	var flipScale
	var flopScale
	// Flip/flop the canvas
	if(flip) flipScale = -1; else flipScale = 1;
	if(flop) flopScale = -1; else flopScale = 1;
	ctx.scale(flipScale, flopScale);

	// Draw the image    
	ctx.drawImage(img, -width/2, -height/2, width, height);

	ctx.restore();
}

//only runs when the canvaspoints need to be *changed*.
//(when the model is changed, or percent speed is altered)
function prep_canvas(relativepoints, dimensions, time, percenttime) {
	
	var minmaxes = minmax(relativepoints, dimensions)
	
	/*var timeinput = document.getElementById("percenttime");
	if(timeinput!=null)
	{
		if(MyLib.globaltime>1)
		{ timeinput.setAttribute("max", Math.ceil(MyLib.globaltime)*100 );	}
		else
		{ timeinput.setAttribute("max", 100);	}
	}*/

	//more points for smaller percent time, to smooth it out.
	//55 points per second, min 80.
	/*var numpoints = Math.floor(55 * time / (percenttime / 100))

	if(numpoints>relativepoints.length)
	{numpoints = relativepoints.length}
	if(numpoints<80)
	{numpoints = 80}
	*/
	var canvaspoints = []
	
	//generate HTML canvas coordinates.
	//Will select a variable number based on the "percent speed" selected.
	//(it won't use all of the relativepoint()'s)
	var i=0;
	//var interval = relativepoints.length / numpoints
	for(var inc=0; inc<relativepoints.length; inc+=1) //interval)
	{
		i=Math.ceil(inc)
		if(i>=relativepoints.length)
		{
			break;
		}
		
		canvaspoints.push(new CanvasPoint(minmaxes, relativepoints[i], dimensions))
	}
		
	//ensure the very last relativepoint gets used, to make it seamless with the floor.
	if(i!=relativepoints.length-1)
	{
		canvaspoints.push(new CanvasPoint(minmaxes, relativepoints[relativepoints.length-1], dimensions))
	}

	
	return { canvaspoints: canvaspoints, minmaxes: minmaxes,};  //set it in the global namespace for the draw functions.
}


//Calculate and store the location of the person and coin at t = time,
//given the rotation rate and slope.
function AbsolutePoints( time, radius, omega, slope, height_start, start_v_x)
{
	//solve for the location of the person and the coin.

	var theta_op = (3* Math.PI / 2) - (omega * time) //person's angle, from origin to person
	
	var x_person = radius * Math.cos(theta_op)
	var y_person = radius * Math.sin(theta_op)

	var x_coin = start_v_x * time
	var y_coin = (slope * x_coin) - (radius - height_start)
	
	this.time = time
	this.x_person = x_person
	this.y_person = y_person
	this.x_coin = x_coin
	this.y_coin = y_coin
}

//If the slope of the ball is not finite (e.g. it is heading directly up with infinite slope),
//then AbsolutePoints() will not work correctly. Here is an alternate function.
function AbsolutePointsVertical( time, radius, omega, slope, height_start, start_v_y)
{
	var theta_op = (3* Math.PI / 2) - (omega * time) //person's angle, from origin to person
	
	var x_person = radius * Math.cos(theta_op)
	var y_person = radius * Math.sin(theta_op)
	
	var x_coin = 0;
	var y_coin = (height_start-radius) + start_v_y * time

	this.time = time
	this.x_person = x_person
	this.y_person = y_person
	this.x_coin = x_coin
	this.y_coin = y_coin	
}


//Take a set of absolute points [(personx, persony), (coinx, coiny)],
//and find the coin's distance relative to the person's feet at (0,0), oriented "upward."
function RelativePoint( points )
{
	var x_diff = points.x_coin - points.x_person
	var y_diff = points.y_coin - points.y_person
	var dist = Math.sqrt( Math.pow( (y_diff), 2) + Math.pow( (x_diff), 2) )
	
	var theta_pc
	var theta_rel
	var x_rel
	var y_rel
	
	//horizontal-person-origin
	var theta_po = Math.atan(points.y_person/points.x_person) 
	
	if(y_diff>0)
	{
		theta_pc = (Math.PI /2) - Math.atan(x_diff / y_diff)
		theta_rel = (theta_pc - theta_po)
		x_rel = -1 * dist * Math.sin(theta_rel)
	}
	else{
		
		if(x_diff>0)
		{
			//horizontal-person-coin
			theta_pc = -1 * Math.atan(y_diff / x_diff)
		}
		else
		{
			theta_pc = Math.atan(x_diff / y_diff) + (Math.PI / 2)
		}
		
		theta_rel = (theta_pc + theta_po) //orign-person-coin
		x_rel = dist * Math.sin(theta_rel)

	}
	
	y_rel = dist * Math.cos(theta_rel)
	
	//rare case if the person traverses an entire half-arc
	//you have to be really playing with the model to get this
	if(points.x_person>0)
	{
		x_rel = x_rel*-1
		y_rel = y_rel*-1
	}
		
	this.dist = dist
	this.x = x_rel
	this.y = y_rel
	this.time = points.time
	//this.x_diff = x_diff
	//this.y_diff = y_diff
	this.theta_pc = theta_pc
	//this.theta_po = theta_po
	//this.theta_rel = theta_rel
}


//Generate point coordinates with respect to the canvas.
function CanvasPoint(minmaxes, x, y, dimensions) {
	//canvasPoint can accept either X and Y coordinates or a RelativePoint.
	if(!(typeof x=="number"))
	{ dimensions = y; y = x.y; x=x.x; }
	
	var canvasX = (((x - minmaxes.minX) / minmaxes.range) * minmaxes.canvasSize) + minmaxes.offsetX
	var canvasY = dimensions.DRAWING_HEIGHT - (( y / minmaxes.range ) * minmaxes.canvasSize )
	
	this.x = canvasX;
	this.y = canvasY;
}

//calculate the min and max values for the displayed canvas.
function minmax(pointlist, dimensions) {
	
	//Ensure, that if these max values are not reached, that
	//these are set to the maxes (otherwise it will find a greater value)
	var minX=pointlist[0].x  //a good starting value is the first one in the list.
	var maxX
	if(pointlist[0].x > 2)
	{ maxX = pointlist[0].x }
	else
	{ maxX = 2 }

	var minY=0 //definitely need to show the original floor. Include y=0 in the range!

	//the coin toss assumes a 6-ft person :-)
	//I'd ideally like to put that person into the window.
	var maxY
	if(dimensions.units=="ft")
	{	maxY=6 	}
	else
	{	maxY = 1.829  } //meters
	
	//Iterate through the given point list to find the min and max values
	//(assuming it supercedes the values I initialized above)
	for(var i=0; i<pointlist.length; i++)
	{
		if(pointlist[i].x < minX)
		{ minX = pointlist[i].x }

		if(pointlist[i].x > maxX)
		{ maxX = pointlist[i].x }

		if(pointlist[i].y < minY)
		{ minY = pointlist[i].y }

		if(pointlist[i].y > maxY)
		{ maxY = pointlist[i].y }
	}
	
	var rangeX = Math.abs(maxX - minX)
	var rangeY = Math.abs(maxY - minY)
	var range
	if(rangeX > rangeY)
	{ range = rangeX }
	else
	{ range = rangeY }
	
	//Ensures the scale stays proportional if height/width are adjusted differently.
	var canvasSize
	var offsetX = 0
	if(dimensions.DRAWING_WIDTH/dimensions.DRAWING_HEIGHT < rangeX/rangeY)
	{ canvasSize = dimensions.DRAWING_WIDTH }
	else
	{ canvasSize = dimensions.DRAWING_HEIGHT

	  if(!dimensions.menuLeftOpen && !dimensions.menuRightOpen)
	  {
		  //keeps it centered perfectly on a wide screen,
		  //but only if the menu is NOT open.
			offsetX = (dimensions.DRAWING_WIDTH - (rangeX / range) * canvasSize) / 2
		  //offsetX = (dimensions.width - (canvasSize+ 2*dimensions.X_BUFFER)) / 2
		  //offsetX = (dimensions.width - (dimensions.DRAWING_WIDTH + 2*dimensions.X_BUFFER)) / 2
	  }
	  else if(dimensions.menuLeftOpen)
	  {
			//Partially fix the offset, but not entirely
			//(counteract the side menu somewhat)
			offsetX = (dimensions.DRAWING_WIDTH - (rangeX / range) * canvasSize) / 4
	  }
	  else
	  {
			offsetX = 3 * (dimensions.DRAWING_WIDTH - (rangeX / range) * canvasSize) / 4
	  }
	}
	
	//used to know which way the silhouette should face :-).
	var directionLeft=true;
	if(pointlist.length>1) //sometimes it is only one point. Don't want it to crash.
	{ 	
		var point_difference=pointlist[1].x-pointlist[0].x
		if( point_difference > 1e-4)  //either very small or positive (to the right)
		{ directionLeft=false; }
	}
	
	var minmaxes = { minX: minX,
				 maxX: maxX,
				 minY: minY,
				 maxY: maxY,
				 range: range, //only one variable for range, to keep x proportional with y.
				 canvasSize: canvasSize,
			 	 pxperfoot: canvasSize / range,
				 directionLeft: directionLeft,
				 offsetX: offsetX,
	}
				 
	return minmaxes
}



//Custom circle-drawing function to render the station floor.

//depending on the parameters, the station could be HUGE, or very small.
	//If it's huge, I don't want to render the entire floor. Too big!
	//If it's small, I DO want to render the entire floor.
	
//I felt that it would be easier to keep it proportional by doing this
		//together with my canvaspoint()'s,
//rather than simply using the context.arc() function.
function draw_floor(ctx, radius, minmaxes, dimensions)
{

	const floor_color = "#007acc"
	const floor_width = 6
	//const space_color = "#9e9e9e"
	const station_color = "white"

	ctx.lineWidth=floor_width;
	ctx.strokeStyle=floor_color;
	ctx.fillStyle=station_color;
	
	var toconvert = 3 * minmaxes.range / radius
	var values
	
	//Double-check here that I'm using valid values for the arc-cosine.
	var floor_start
	var floor_end
	if(toconvert < 1)
	{
		floor_start = Math.acos( toconvert )
		floor_end = Math.acos( -1 * toconvert )
	}
	else{
		floor_start = 0
		floor_end = Math.PI
	}
	
	
	var arc = floor_end - floor_start

	var x
	var y
	var i
	var bottomcurve = [] //points for bottom half of circle
	for(i=floor_start; i<floor_end; i+=arc/100)
	{	
		x = radius * Math.cos(i)
		y = -1 * radius * Math.sin(i) + radius //-1, bottom arc!
		values = new CanvasPoint(minmaxes, x, y, dimensions)
		bottomcurve.push(values)
	}

	//once more AT floor_end, so there won't be a break if I render the top.
	x = radius * Math.cos(floor_end)
	y = -1 * radius * Math.sin(floor_end) + radius //-1, bottom arc!
	values = new CanvasPoint(minmaxes, x, y, dimensions)
	bottomcurve.push(values)


	//if the top of the station is close to actually appearing, then render it.
	x=radius*Math.cos(0)
	y=radius*Math.sin(0) + radius
	values = new CanvasPoint(minmaxes, x, y, dimensions)
	if(values.y>-50)
	{
		//generate points for top curve
		var topcurve = []
		for(i=floor_end; i>floor_start; i-=arc/100) //the reverse direction!
		{	
			x = radius * Math.cos(i)
			y = 1 * radius * Math.sin(i) + radius //+1, top arc!
			values = new CanvasPoint(minmaxes, x, y, dimensions)
			topcurve.push(values)
		}
		
		//START by filling outer-space around the curve!
		ctx.beginPath()

		ctx.moveTo(bottomcurve[0].x+dimensions.X_BUFFER, bottomcurve[0].y+dimensions.Y_BUFFER)
		
		for(i=1; i<bottomcurve.length; i+=1)
		{
			ctx.lineTo(bottomcurve[i].x+dimensions.X_BUFFER, bottomcurve[i].y+dimensions.Y_BUFFER)
		}
		
		//ctx.moveTo(topcurve[0].x+dimensions.X_BUFFER, topcurve[0].y+dimensions.Y_BUFFER)
		
		for(i=0; i<topcurve.length; i+=1)
		{
			ctx.lineTo(topcurve[i].x+dimensions.X_BUFFER, topcurve[i].y+dimensions.Y_BUFFER)
		}

		ctx.closePath()
		ctx.fill()
		ctx.stroke()
	}
	else //no top curve
	{
		//START by filling outer-space around the curve!
		ctx.beginPath()
		ctx.moveTo(bottomcurve[0].x+dimensions.X_BUFFER, bottomcurve[0].y+dimensions.Y_BUFFER)
		
		for(i=1; i<bottomcurve.length; i+=1)
		{
			ctx.lineTo(bottomcurve[i].x+dimensions.X_BUFFER, bottomcurve[i].y+dimensions.Y_BUFFER)
		}
		
		i-=1; //set i=last element to make this less tedious
		if(bottomcurve.length>0)
		{
			//create a box around the top of the screen.
			ctx.lineTo(-1, -1);
			ctx.lineTo(dimensions.width+2,-1);
			ctx.closePath()
			ctx.fillStyle=station_color;
			ctx.fill()
		}
		
		ctx.stroke()
		
	}
	

	//reset the stroke
	ctx.lineWidth=1;	
	ctx.strokeStyle="#000000";
	ctx.fillStyle="#000000";
}




export default FullCanvas;


