import React, { Component } from 'react';
import './App.css';
import ReactDOM from 'react-dom'


class AngleInput extends Component {
	
	constructor(props) {
      super(props);
		
      this.state = { focused:false, }
	  this.onFocus = this.onFocus.bind(this);
	  this.onBlur = this.onBlur.bind(this);
	}
	
	onFocus() { this.setState({focused:true,})	}

	onBlur() { this.setState({focused:false,})	}
	
	shouldComponentUpdate(newProps, newState)
	{
		//If the only change was focusing the field, no point in updating unless the value is zero.
		//(blanks the line)
		if( (newState.focused===true && this.state.focused===false && this.props.value !==0) ||
			(newState.focused===false && this.state.focused===true && this.props.value !==0) )
			{  //console.log("false")
				return false  }
		else if( this.props.value===newProps.value
				&& this.state.focused===newState.focused
				&& this.props.min===newProps.min
				&& this.props.max===newProps.max
				&& this.props.display===newProps.display
				&& this.props.units===newProps.units)
			{  //console.log("nothing changed")
				return false  }
		else
		{  //console.log("true")
			return true   }
	}
	
	
	componentDidMount()
	{ this.componentDidUpdate(); }
	
	componentDidUpdate()
	{
		let canvas = ReactDOM.findDOMNode(this.refs.compass);
		let ctx = canvas.getContext('2d');
		
		ctx.clearRect(0, 0, 20, 20);
		
		ctx.beginPath();
		ctx.strokeStyle="#000000";	
		ctx.arc(10, 10, 10, 0, 2 * Math.PI, false);
		
		var computedangle = (this.props.value * Math.PI / 180);
		var x = 10 * Math.sin(computedangle)
		var y = 10 * Math.cos(computedangle)
		
		ctx.moveTo(10,10)
		ctx.lineTo(10-x, 10+y)
		ctx.stroke();

		ctx.lineWidth=2		
		ctx.beginPath();
		ctx.strokeStyle="#e02e0b";
		ctx.moveTo(10,10)
		ctx.lineTo(10+x, 10-y)
		ctx.stroke();		
	}

	
	render() {
		
	var className
	if(this.props.display===false)
	{
		className="hide";
	}
	else
	{
		className="InputBox";
	}
	
	
	var UnitsStyle
	if(this.props.units!=null)
	{
		UnitsStyle={}
	}
	else
	{
		UnitsStyle={
			display:"none",
		}
	}
	
	const compassStyle= {
		marginLeft:"0px",
		marginTop:"4px",
		backgroundColor:"white",
		borderRadius:'50%',
		padding:'1px',
	}

	var value=this.props.value;
	if(this.props.value===0 && this.state.focused)  //otherwise you are never able to backspace entirely. Frustrating.
	{  value="";  }

	
	const spanstyle={
		fontSize:'14px',
		color:'#adccff',
		//width:'155px',
		textAlign:'center',
	}
	
	const blurbwidth={
		//width:'220px',
	}
	
	var simplified=""
	var simplified2=""
	var plural="s"
	
	if(this.props.droppedflag)
	{  simplified="(dropped)" }
	else if(this.props.value===0)
	{  simplified="(thrown straight up)"	}
	else if(this.props.value===90)
	{  simplified="(thrown straight right"
	   simplified2="or opposite the station)" }
	else if(this.props.value===-90)
	{  simplified="(thrown straight left"
	   simplified2="or with the station)" }
	else if(Math.abs(this.props.value)===180)
	{  simplified="(thrown straight down)" }
	else if(this.props.value > 0 && this.props.value < 45)
	{  if(this.props.value===1){plural=""}
	   simplified="(thrown " + this.props.value.toString() + " degree" + plural +" to the right)" }
	else if(this.props.value < 0 && this.props.value > -45)
	{  if(this.props.value===-1){plural=""}
	   simplified="(thrown " + Math.abs(this.props.value).toString() + " degree" + plural +" to the left)" }
	else if(this.props.value > 90)
	{  if(this.props.value===91){plural=""}
	   simplified="(thrown " + (this.props.value-90).toString() + " degree" + plural +" down," 
	   simplified2="to the right or opposite the station)" }
	else if(this.props.value < -90)
	{  if(this.props.value===-91){plural=""}
	   simplified="(thrown " + ((-1*this.props.value)-90).toString() + " degree" + plural +" down," 
	   simplified2="to the left or with the station)" }
	else if(this.props.value >= 45 && this.props.value < 90)
	{  if(this.props.value===89){plural=""}
	   simplified="(thrown " + (90-this.props.value).toString() + " degree" + plural +" up,"
	   simplified2="to the right or opposite the station)" }
	else if(this.props.value <= -45 && this.props.value > -90)
	{  if(this.props.value===-89){plural=""}
	   simplified="(thrown " + (90-(-1*this.props.value)).toString() + " degree" + plural +" up,"
	   simplified2="to the left or with the station)" }
	/*else if(Math.abs(this.props.value) > 90)
	{ simplified="(thrown " + (Math.abs(this.props.value)-90).toString() + " degrees down)" }*/
	/*else if(Math.abs(this.props.value) >= 80 && Math.abs(this.props.value) < 90)
	{ simplified="(thrown " + (90-Math.abs(this.props.value)).toString() + " degrees up)"}*/
	

	return(
		<div className={className}>{this.props.title}
			<table><tbody>
				<tr><td><canvas ref="compass" style={compassStyle} width="20" height="20"/>&nbsp;</td>
					<td>
						<input type='number' className="InputStyle" onFocus={this.onFocus} onBlur={this.onBlur} value={value} step="any" min={this.props.min} max={this.props.max} onChange = {this.props.updateState} id={this.props.variable}/><div className="UnitsStyle" style={UnitsStyle}>{this.props.units}</div>
					</td>
					<td><input type='range' className="slider" min={this.props.min} max={this.props.max} value={this.props.value} onChange = {this.props.updateState} /></td>
				</tr>
			</tbody></table>
			<div style={blurbwidth}><div style={spanstyle}>{simplified}<br/>{simplified2}</div></div>
		</div>
	  );
	}
}

export default AngleInput;







